.Type, .Options {
    /* margin: 10px; */
    display:flex;
    flex-flow: column nowrap;
    width:100%;
    align-items: center;
    
  }

.Type > div{
    opacity: .8;
    width:250px;
} 

.Type .Group4{
    display: flex;
    flex-flow: row nowrap;
}

.Type .Group4 > div{
    width:62.5px;
}

