 .Type input[type="radio"]{
      opacity: 0;
     position: fixed;
     width: 250px;
     height:40px;
   }

   .Type label {
    display: inline-block;
    background-color: #ddd;
    border: 2px solid #444;
    border-radius: 4px;
    color: #000;
      width:250px;
      height:40px;
      text-align: center;
      padding: 10px 0px;
  } 

  .Type .Group4 input[type="radio"],.Type .Group4 label{
      width:62.5px;
      
  }

